.main-container {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.header {
	font-size: 11vh;
	font-family: Megrim;
	color: white;
	text-align: center;
	text-shadow: 0 0 4px white,
				0 0 10px white,
				0 0 18px white,
				0 0 38px #ff0000,
				0 0 73px #ff0000,
				0 0 90px #ff0000;
	margin: .4em 0;
}

.section-wrapper {
	padding-top: 51px;
	height: calc(100vh - 51px);
	display: flex;
	align-items: center;
	justify-content: center;
}

.home {
	height: 88vh;
	flex-direction: column;
	background-color: transparent;
	padding-top: 0;
	padding-bottom: calc(12vh - 51px);
}

.home-button-container > a {
	background-color: transparent;
	transition: color 0.3s;
	color: rgba(255, 255, 255, 0.527);
	font-size: 4vh;
	border: 0;
	padding: 0px 20px;
}

.home-button-container > a:hover {
	cursor: pointer;
	color: rgb(255, 255, 255);
}

.home-button-container > a:hover > * {
	-webkit-filter: /*drop-shadow(0 0 4px rgb(255, 255, 255))
	drop-shadow(0px 0 12px rgba(253, 134, 134, 0.829));*/
	drop-shadow(0 0 3px rgb(255, 255, 255))
			drop-shadow(0px 0 10px rgb(253, 134, 134));
	        filter: /*drop-shadow(0 0 4px rgb(255, 255, 255))
	drop-shadow(0px 0 12px rgba(253, 134, 134, 0.829));*/
	drop-shadow(0 0 3px rgb(255, 255, 255))
			drop-shadow(0px 0 10px rgb(253, 134, 134));
}

.home-button-container > a > * {
	-webkit-filter: drop-shadow(0 0 3px rgb(255, 255, 255))
			drop-shadow(0px 0 10px rgba(253, 134, 134, 0.719));
	        filter: drop-shadow(0 0 3px rgb(255, 255, 255))
			drop-shadow(0px 0 10px rgba(253, 134, 134, 0.719));
			/*drop-shadow(0 0 3px rgba(207, 207, 207, 0.164))*/
			/*drop-shadow(0px 0 10px rgba(253, 134, 134, 0.973));*/
}

.home-button-container > a.shuttle-active {
	position: fixed;
	-webkit-transform-origin: 0% 0%;
	        transform-origin: 0% 0%;
	transition: left 6s, top 6s, -webkit-filter 2s, -webkit-transform 1s linear;
	transition: filter 2s, transform 1s linear, left 6s, top 6s;
	transition: filter 2s, transform 1s linear, left 6s, top 6s, -webkit-filter 2s, -webkit-transform 1s linear;
	-webkit-filter: none;
	        filter: none;
}

.home-button-container > a.shuttle-active > * {
	position: relative;
	top: -0.5em;
	left: calc(-20px - 50%);
	/*filter: drop-shadow(-2px 0 2px yellow)
			drop-shadow(-6px 0 5px rgba(255, 105, 105, 0.719));*/
}

