@-webkit-keyframes flicker {
	0%, 3%, 33%, 36%, 75%, 78%, 84%, 100% {
		color: white;
		text-shadow: 0 0 4px white,
					0 0 10px white,
					0 0 18px white,
					0 0 38px #ff0000,
					0 0 73px #ff0000,
					0 0 90px #ff0000;
	}

	 1%, 34%, 76%, 85%, 99% {
		 color:rgba(255, 255, 255, 0.03);
		 text-shadow: none;
	 }
}

@keyframes flicker {
	0%, 3%, 33%, 36%, 75%, 78%, 84%, 100% {
		color: white;
		text-shadow: 0 0 4px white,
					0 0 10px white,
					0 0 18px white,
					0 0 38px #ff0000,
					0 0 73px #ff0000,
					0 0 90px #ff0000;
	}

	 1%, 34%, 76%, 85%, 99% {
		 color:rgba(255, 255, 255, 0.03);
		 text-shadow: none;
	 }
}

@-webkit-keyframes rainbow {
	0%, 100% {
		text-shadow: 0 0 4px white,
					0 0 10px white,
					0 0 18px white,
					0 0 38px #ff0000,
					0 0 73px #ff0000,
					0 0 90px #ff0000;
	}

	50% {
		text-shadow: 0 0 4px white,
					0 0 10px white,
					0 0 18px white,
					0 0 38px #4e4eec,
					0 0 73px #4e4eec,
					0 0 90px #4e4eec;
	}
}

@keyframes rainbow {
	0%, 100% {
		text-shadow: 0 0 4px white,
					0 0 10px white,
					0 0 18px white,
					0 0 38px #ff0000,
					0 0 73px #ff0000,
					0 0 90px #ff0000;
	}

	50% {
		text-shadow: 0 0 4px white,
					0 0 10px white,
					0 0 18px white,
					0 0 38px #4e4eec,
					0 0 73px #4e4eec,
					0 0 90px #4e4eec;
	}
}

@-webkit-keyframes shuttle-burner {
	0%, 100% {
		-webkit-filter: none;
		        filter: none;
	}
	50%, 75% {
		-webkit-filter: drop-shadow(-2px 0 2px yellow)
			drop-shadow(-6px 0 5px rgba(255, 105, 105, 0.719));
		        filter: drop-shadow(-2px 0 2px yellow)
			drop-shadow(-6px 0 5px rgba(255, 105, 105, 0.719));
	}
}

@keyframes shuttle-burner {
	0%, 100% {
		-webkit-filter: none;
		        filter: none;
	}
	50%, 75% {
		-webkit-filter: drop-shadow(-2px 0 2px yellow)
			drop-shadow(-6px 0 5px rgba(255, 105, 105, 0.719));
		        filter: drop-shadow(-2px 0 2px yellow)
			drop-shadow(-6px 0 5px rgba(255, 105, 105, 0.719));
	}
}

@media (prefers-reduced-motion: no-preference) {
	.flicker1 {
		-webkit-animation: flicker 5s infinite linear;
		        animation: flicker 5s infinite linear;
	}

	.header {
		-webkit-animation: rainbow 100s infinite linear;
		        animation: rainbow 100s infinite linear;
	}

	.main-container {
		-webkit-animation: scroll 3s 5s linear forwards;
		        animation: scroll 3s 5s linear forwards;
	}
}

